<template>
    <div class="inputBox shadow">
        <input type="text" v-model="newTodoItem" @keyup.enter="addTodo" placeholder="Type what you have to do.">
        <span class="addContainer" @click="addTodo">
            <i class="addBtn fas fa-plus" aria-hidden="true"></i>
        </span>
    </div>
</template>

<style scoped>
    .input:focus { outline: none; }
    .inputBox { 
        background-color: #fff;
        height: 50px;
        line-height: 50px;
        border-radius: 5px;
        display: flex;
    }
    .inputBox input {
        border-style: none;
        font-size: 0.9rem;
        flex-grow: 1;
        padding: 0 10px;
    }
    .addContainer {
        background: linear-gradient(to right, #6478fb, #8763fb);
        width: 3rem;
        border-radius: 0 5px 5px 0;
        display: block;
    }
    .addContainer > .addBtn {
        color: #fff;
    }
</style>

<script>
export default {
    data() {
        return {
            newTodoItem: ''
        }
    },
    methods: {
        addTodo() {
            if(this.newTodoItem.trim() !== '') { //값이 비어있으면 return 보냄
            const value = this.newTodoItem.trim(); //trim: 양쪽에 있는 빈 칸 지움
            // localStorage.setItem(value, value);
            this.$emit('childAddTodo', value);
            }else{
                this.$emit('showModal');
            }
            this.newTodoItem = '';
        },
    }
}
</script>
